import React, { lazy, Suspense } from 'react';
import { Routes, Route } from 'react-router-dom';

// Core routes (always available)
const UnifiedDashboard = lazy(() => import('../pages/Dashboard'));
const EmployeeSurvey = lazy(() => import('../pages/EmployeeSurvey'));
const CreateSurvey = lazy(() => import('../pages/CreateSurvey'));
const SurveysList = lazy(() => import('../pages/SurveysList'));

// Feature-gated routes (only import existing components)
const PhotoSurveyDemo = lazy(() => import('../pages/PhotoSurveyDemo'));

// Feature flags are declared in types/feature-flags.d.ts

const FeatureGatedRouter: React.FC = () => {
  return (
    <Suspense fallback={<div className="min-h-screen flex items-center justify-center">Loading...</div>}>
      <Routes>
        {/* Core MVP Routes - Always Available */}
        <Route path="/dashboard" element={<UnifiedDashboard />} />
        <Route path="/survey" element={<EmployeeSurvey />} />
        <Route path="/create" element={<CreateSurvey />} />
        <Route path="/surveys" element={<SurveysList />} />
        
        {/* Feature-gated routes - Only load if feature flags are enabled */}
        {__FEATURE_PHOTO__ && (
          <Route path="/photo" element={<PhotoSurveyDemo />} />
        )}
        
        {__FEATURE_ADMIN__ && (
          <Route path="/admin" element={<div>Admin Dashboard (Coming Soon)</div>} />
        )}
        
        {__FEATURE_AUTOPILOT__ && (
          <Route path="/auto-pilot" element={<div>Auto-Pilot Dashboard (Coming Soon)</div>} />
        )}
        
        {__FEATURE_ADVANCED_ANALYTICS__ && (
          <Route path="/analytics" element={<div>Advanced Analytics (Coming Soon)</div>} />
        )}
        
        {__FEATURE_INTEGRATIONS__ && (
          <Route path="/integrations" element={<div>Integrations (Coming Soon)</div>} />
        )}
        
        {/* Pro features */}
        {__FEATURE_PRO__ && (
          <>
            <Route path="/pro-dashboard" element={<UnifiedDashboard />} />
            {/* Add other pro-specific routes here */}
          </>
        )}
        
        {/* Export features */}
        {__FEATURE_EXPORTS__ && (
          <>
            <Route path="/exports" element={<div>Export Dashboard (Coming Soon)</div>} />
            {/* Add export-specific routes here */}
          </>
        )}
      </Routes>
    </Suspense>
  );
};

export default FeatureGatedRouter;
